    para: 'Lands of our Ancestors was reviewed and endorsed by the Director of the Santa Ynez  Chumash Culture Department (Nakia Zavalla), a Chumash Language Teacher and member  of the California Indian Education Oversight Committee (Kathleen Marshall) and the Santa  Ynez Chumash Elders Board'
